<h1>radio单选框</h1>
<p>在一组备选项中进行单选</p>
<h2>基础用法</h2>
<p>由于选项默认可见，不宜过多，若选项过多，建议使用 Select 选择器。</p>
<p>要使用 <code v-pre="">Radio</code> 组件，只需要设置<code v-pre="">v-model</code>绑定变量，选中意味着变量的值为相应 Radio <code v-pre="">label</code>属性的值，label可以是<code v-pre="">String</code>、<code v-pre="">Number</code>或<code v-pre="">Boolean</code>。</p>
<div class="demo-block">
  <lee-radio v-model="radio" label="1" name="name">备选项</lee-radio>
  <lee-radio v-model="radio" label="2" name="name">备选项</lee-radio>
  <lee-radio v-model="radio" label="3" name="name">备选项</lee-radio>
</div>
<demo-block>
