<span class="hljs-tag">&lt;<span class="hljs-name">lee-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">lee-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">lee-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"name"</span>&gt;</span>备选项<span class="hljs-tag">&lt;/<span class="hljs-name">lee-radio</span>&gt;</span>
