<h2>单选框组</h2>
<p>适用于在多个互斥的选项中选择的场景
结合<code v-pre="">lee-radio-group</code>元素和子元素<code v-pre="">lee-radio</code>可以实现单选组，在<code v-pre="">lee-radio-group</code>中绑定<code v-pre="">v-model</code>，在<code v-pre="">lee-radio</code>中设置好<code v-pre="">label</code>即可，无需再给每一个<code v-pre="">lee-radio</code>绑定变量，另外，还提供了<code v-pre="">change</code>事件来响应变化，它会传入一个参数<code v-pre="">value</code>。</p>
<div class="demo-block">
  <lee-radio-group v-model="radio2">
    <lee-radio :label="1" name="name2">备选项</lee-radio>
    <lee-radio :label="2" name="name2">备选项</lee-radio>
    <lee-radio :label="3" name="name2">备选项</lee-radio>
  </lee-radio-group>
</div>
