<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>绑定值变化时触发的事件</td>
<td>选中的 Radio label 值</td>
</tr>
</tbody>
</table>
